<template>
  <div id="app">
    <img src="./assets/BUNDlogo2012-1.png" alt="BUND logo" width="150px">
    <h1>{{ msg }}</h1>
<!--    <h2>Essential Links</h2>
    <ul>
      <li><a href="https://vuejs.org" target="_blank">Core Docs</a></li>
      <li><a href="https://forum.vuejs.org" target="_blank">Forum</a></li>
      <li><a href="https://gitter.im/vuejs/vue" target="_blank">Gitter Chat</a></li>
      <li><a href="https://twitter.com/vuejs" target="_blank">Twitter</a></li>
    </ul>
    <h2>Ecosystem</h2>
    <ul>
      <li><a href="http://router.vuejs.org/" target="_blank">vue-router</a></li>
      <li><a href="http://vuex.vuejs.org/" target="_blank">vuex</a></li>
      <li><a href="http://vue-loader.vuejs.org/" target="_blank">vue-loader</a></li>
      <li><a href="https://github.com/vuejs/awesome-vue" target="_blank">awesome-vue</a></li>
    </ul>-->
    <table id="main-filter-table">
        <tr>
            <td>
                <table class="my-filter-column">
                    <tr>
                        <td>
                            <!--DOC: https://developer.mozilla.org/en-US/docs/Web/HTML/Element/input/checkbox-->
                            <div>
                                <input type="checkbox" id="filter-category-laden" value="1" v-model="filterCategoryLaden">
                                <label for="filter-category-laden">Laden</label>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <input type="checkbox" id="filter-category-restaurant" value="1" v-model="filterCategoryRestaurant">
                            <label for="filter-category-restaurant">Restaurant</label>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <input type="checkbox" id="filter-category-direktvermarkter" value="1" v-model="filterCategoryDirektvermarkter">
                            <label for="filter-category-direktvermarkter">Direktvermarkter</label>
                        </td>
                    </tr>
                </table>
            </td>
            <td>
                <table class="my-filter-column">
                    <tr>
                        <td>
                            <div>
                                <input type="checkbox" id="filter-eigenschaft-allesbio" value="1" v-model="filterEigenschaftAllesBio">
                                <label for="filter-eigenschaft-allesbio">Alles Bio</label>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <input type="checkbox" id="filter-eigenschaft-minvegetarisch" value="1" v-model="filterEigenschaftMinVegetarisch">
                            <label for="filter-eigenschaft-minvegetarisch">Mindestens vegetarisch</label>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <input type="checkbox" id="filter-eigenschaft-allesvegan" value="1" v-model="filterEigenschaftAllesVegan">
                            <label for="filter-eigenschaft-allesvegan">Alles vegan</label>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <input type="checkbox" id="filter-eigenschaft-unverpackt" value="1" v-model="filterEigenschaftUnverpackt">
                            <label for="filter-eigenschaft-unverpackt">Unverpackt / wenig Plastik</label>
                        </td>
                    </tr>
                </table>
            </td>
            <td>
                <table class="my-filter-column">
                    <tr>
                        <td>
                            <div>
                                <input type="checkbox" id="filter-produkte-backwaren" value="1" v-model="filterProdukteBackwaren">
                                <label for="filter-produkte-backwaren">Backwaren</label>
                            </div>
                        </td>
                    </tr>

                </table>

            </td>
            <td>
                <!--<my-filter-checkbox name="filter-category-restaurant" value="1" label="Restaurant" v-model="filterCategoryRestaurant"></my-filter-checkbox>-->
            </td>
        </tr>
    </table>

    <!--Ergebnisse-->
    <!-- DOC: bootstrap containers: https://bootstrap-vue.js.org/docs/layout/
            "<b-container fluid> for full width."
    -->
    <b-container fluid>
        <b-row>
            <b-col>
                <!--Ergebnis-Tabelle-->
                <!-- nice table style with bootstrap: add table class
                DOC: https://v4-alpha.getbootstrap.com/content/tables/#striped-rows-->
                <table class="table table-bordered table-striped">
                    <!-- DOC: https://vuejs.org/v2/guide/list.html#v-for-with-v-if -->
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>PLZ</th>
                        <th>Stadt</th>
                        <th>Kategorie</th>
                        <th>Aktion</th>
                    </tr>
                </thead>
                <tbody>
                <!-- Highlight active row: DOC: https://v4-alpha.getbootstrap.com/content/tables/#contextual-classes -->
                <tr v-for="item in filteredAnbieterData()" class="result-table-tr" v-bind:class="{ 'table-active': isCurrentItem(item) }">
                    <td>{{ item.name }}</td>
                    <td>{{ item.plz }}</td>
                    <td>{{ item.city }}</td>
                    <td>{{ item.category }}</td>
                    <td><a href="#" v-on:click.prevent="showDetails(item)">Details</a> <!--<span v-if="isCurrentItem(item)">-></span>--></td>
                </tr>
                </tbody>
                </table>
            </b-col>
            <!--Details und Karte-->
            <b-col>
                <div class="ratgeber-detail-view" v-if="showDetailsForId > 0">
                    <br/>
                    Infos zu
                    <div>
                    <div style="margin-left: 50px">
                        <h3>{{ detailsItemData.name }}</h3>
                        Ort: <b>{{ detailsItemData.city }}</b>
                        <p>
                        <b>Produktüberblick</b>: TODO
                        </p>
                        <p>
                        <b>Merkmale</b>:
                        <span v-if="detailsItemData.allesbio">[alles bio]</span>
                        <span v-if="detailsItemData.min_vegetarisch">[min. vegetarisch]</span>
                        <span v-if="detailsItemData.allesvegan">[alles vegan]</span>
                        </p>
                        <p>
                        <b>Produkte und Besonderheiten</b>
                        </p><ul>
                                <li>TODO</li>
                        </ul>
                        <p></p>
                        <p>
                        <b>Kontakt:</b> Telefon: {{ detailsItemData.phone }}
                        </p>
                    </div>
                    </div>
                    <p>Debug: show details for {{showDetailsForId}}</p>
                </div>
            </b-col>
            <b-col>
                <p>...Karte...</p>
                <div id="mapid"></div>
            </b-col>
        </b-row>
    </b-container>
    <div style="text-align: left">
        Debug-Optionen: <input type="checkbox" v-model="debugShowAnbieterData">Zeige anbieterData</input>
        <div v-if="debugShowAnbieterData">
            {{ anbieterData }}
        </div>
        <my-component></my-component>
        <hello-world></hello-world>
    </div>
  </div>
</template>

<script>
// DOC: example imports: https://github.com/prograhammer/vue-example-project/blob/master/src/components/App.vue
import RatgeberMap from './ratgebermap.js'

export default {
  name: 'app',
  data () {
    return {
      msg: 'Einkaufsratgeber Ernährung',
      filterCategoryLaden: true,
      filterCategoryRestaurant: true,
      filterCategoryDirektvermarkter: true,
      filterEigenschaftAllesBio: true,
      filterEigenschaftMinVegetarisch: true,
      filterEigenschaftAllesVegan: false,
      filterEigenschaftUnverpackt: false,
      filterProdukteBackwaren: false,
      showDetailsForId: 0,
      detailsItemData: null,
      anbieterData: getAnbieterData(),
      debugShowAnbieterData: false
    }
  },
  // DOC: Instance lifecycle hooks - https://vuejs.org/v2/guide/instance.html
  mounted: function () {
    RatgeberMap.init();
    this.resetDetailsView();
  },
  // DOC: https://vuejs.org/v2/guide/computed.html
  methods: {
    filteredAnbieterData: function() {
        // DOC: https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/filter
        return this.anbieterData.filter(this.applyFilter);
    },

    applyFilter: function(item) {
        if (item.category == 'Direktvermarkter' && !this.filterCategoryDirektvermarkter) {
            return false;
        }
        if (item.category == 'Laden' && !this.filterCategoryLaden) {
            return false;
        }
        if (item.category == 'Restaurant' && !this.filterCategoryRestaurant) {
            return false;
        }
        if (!item.allesbio && this.filterEigenschaftAllesBio) {
            return false;
        }
        if (!item.allesvegan && this.filterEigenschaftAllesVegan) {
            return false;
        }
        // Hier muss das vegane berücksichtigt werden.
        // TODO: entweder radioboxen oder automatisch das min_veg abwählen, wenn vegan gewählt wird. Oder Slider.
        if (!item.min_vegetarisch && !item.allesvegan && this.filterEigenschaftMinVegetarisch) {
            return false;
        }
        if (!item.hat_backwaren && this.filterProdukteBackwaren) {
            return false;
        }
        return true;
    },
    showDetails: function(item) {
        this.showDetailsForId = item.id;
        this.detailsItemData = item;

        if (item.id > 0) {
            // TODO: center on map and make do things properly
            //   derzeit: TypeError: t is undefined[Weitere Informationen]
            RatgeberMap.addAndShowMarker(item.name, item.website, item.plz, item.city, item.latlng);
            //RatgeberMap.sampleAddAndZoomMarkers();
        }
    },
    isCurrentItem: function(item) {
        return this.showDetailsForId == item.id;
    },
    resetDetailsView: function() {
        this.showDetails(0);
        // collect all coordinates out of the currently filtered data and zoom to them:
        RatgeberMap.zoomToAllPlaces(
            this.filteredAnbieterData().map(function(item) {
                return item.latlng;
            })
        );
    }
  },
  watch: {
    // reset view if one of the filters is toggled
    filterCategoryLaden: function (val) { this.resetDetailsView(); },
    filterCategoryRestaurant: function (val) { this.resetDetailsView(); },
    filterCategoryDirektvermarkter: function (val) { this.resetDetailsView(); },
    filterEigenschaftAllesBio: function (val) { this.resetDetailsView(); },
    filterEigenschaftMinVegetarisch: function (val) { this.resetDetailsView(); },
    filterEigenschaftAllesVegan: function (val) {
        this.resetDetailsView();
        if (val = true) this.filterEigenschaftMinVegetarisch = false; /* TODO: logic not yet complete */
    },
    filterEigenschaftUnverpackt: function (val) { this.resetDetailsView(); },
    filterProdukteBackwaren: function (val) { this.resetDetailsView(); },
  }
}

/**
 * should be called only once
 */
var getAnbieterData = function() {
    // DOC: https://maketips.net/tip/223/how-to-include-jquery-into-vuejs
    // var $ = window.jQuery = require('jquery');
    // NOTE: !!! not needed when using WebPack's require!!! So we do not add "jquery": "^3.2.1" to package.json
    // DOC: http://api.jquery.com/jquery.getjson/
//     $.getJSON("./assets/export-data-table-2.json", function(data) {
//         console.log(data);
//     })  .done(function() {
//    ...

    // NOTE: for the .json ext we need to modifiy the webpack.config.js, see there and https://stackoverflow.com/questions/27639005/how-to-copy-static-files-to-build-directory-with-webpack
    var dataFile = require("./assets/export-data-table-2.json");
    console.log("json loaded");
    return dataFile.data;
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

table#main-filter-table {
    /* DOC: center table: https://stackoverflow.com/questions/7059394/how-to-position-a-table-at-the-center-of-div-horizontally-vertically */
    width: 50%; margin: auto;
}

table.my-filter-column {
    text-align: left;
}

.result-table-tr td {
    text-align: left;
}

.ratgeber-detail-view {
    text-align: left;
}
#mapid {
    height: 400px;
    width: 800px;
}
</style>



